form
{
    flex: 1;
    padding: 10px;
}
form div 
{
    margin-bottom: 10px;
}
form input
{
    display: block;
    width: 100%;
    border : 1px solid grey ;
    height: 40px;
    padding: 5px;
}

form input[type="submit"]
{
    cursor: pointer;
    background-color: brown;
    color: white;
    border: 0px;
}